/**
 * @description: 新增汇总表
 */
import React ,{ Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Button, Input, Select, Modal, Upload, message } from 'antd';
import FieldModal from './addOrEditFieldModal';
import styles from './index.module.less';
import SystemTabs from '../common/systemTabs';
import { getAction, postAction } from '@/axios';

class Add extends Component {
    constructor(props){
        super(props);
        this.state={
            templetId:"",//当前编辑的模板id
            dataSetList:[//数据集下拉项
                // {label:"数据集一",value:"001"},
            ],
            dataSet:"001",//数据集
            name:"",//汇总表名称
            tableHead:[//表头结构
                // {title:"111",dataIndex:"001",fieldName:"tag01"},
            ],
            selectHeadInfo:{},//当前选择的表头信息
            fileList:[],//上传的模板文件
            fileName:"",//原模板名称
            showAddFieldModal:false,//添加汇总表字段弹窗
            type:"add",//添加汇总表字段弹窗类型:编辑/新增
            editIndex:0,//当前编辑的数据index
        }
    }
    componentDidMount(){
        let url = window.location.toString();
        let arrObj = url.split("?");
        let templetId = "";
        if(arrObj.length>1){
            let arrParam = arrObj[1].split("&");
            for(let i=0;i<arrParam.length;i++){
                let arr = arrParam[i].split("=");
                if(arr !==null && arr[0] === "templetId"){
                    templetId = arr[1];
                }
            }
        }
        this.setState({templetId});
        this._getInitInfoByIdRequest(templetId);
        this._getDataSetRequest();
    }
    //根据templetId获取初始信息
    _getInitInfoByIdRequest = (templetId = "") => {
        // console.log(templetId)
        getAction(`/ybt/summary/info?templetId=${templetId}`).then(res=>{
            // console.log(res);
            if(res.success){
                this.setState({
                    dataSet:res.obj.dataSetId,
                    name:res.obj.templetName,
                    tableHead:JSON.parse(res.obj.columnConfigJson),
                    fileName:res.obj.templateFileName
                })
            }
        })
    }
    //获取数据集请求
    _getDataSetRequest = () => {
        getAction(`/ybt/data/dataset/folder/list?dataSetFolderId=&dataSetName=&dataSetType=2&pageNum=0&pageSize=0`).then(res=>{
            if(res.success){
                this.setState({
                    dataSetList:res.obj.rows.map(item=>({label:item.dataSetName,value:item.dataSetId}))
                })
            }  
        })
    }
    //切换数据集
    _changeSelect = (value) => {
        Modal.confirm({
            title:"确认修改数据集吗？",
            content:"修改数据集将重置已配置的表头数据",
            onOk:()=>{
                this.setState({
                    dataSet:value,
                    tableHead:[]
                })
            },
            okText:"确定",
            cancelText:"取消"
        })
    }
    //新增/编辑 按钮
    _showFieldModal = (type) => {
        if(type==="add"){
            this.setState({
                showAddFieldModal:true,
                selectHeadInfo:{},
                type
            })
        }else{
            if(JSON.stringify(this.state.selectHeadInfo) === "{}"){
                message.warning("请先选择一条需要修改的列");
                return false;
            }
            this.setState({
                showAddFieldModal:true,
                type
            })
        }
    }
    
    //点击选择表头
    _selectHead = (record,index) => {
        this.setState({selectHeadInfo:record,editIndex:index})
    }
    //修改汇总表名称
    _updateName = (e) => {
        this.setState({name:e.target.value})
    }
    //确定
    _submit = () => {
        if(this.state.tableHead.length===0){
            message.warning("最少设置一个表头");
            return false
        }
        if(this.state.name===""){
            message.warning("汇总表名称不能为空");
            return false
        }
        // if(this.state.fileList.length===0){
        //     message.warning("请上传模板");
        //     return false
        // }
        // console.log(this.state.dataSetList.find(item => item.value===this.state.dataSetId))
        let params = {
            templetId:this.state.templetId,
            templetName:this.state.name,
            dataSetId:this.state.dataSet,
            dataSetName:this.state.dataSetList.find(item => item.value===this.state.dataSet).label,
            columnConfigJson:this.state.tableHead,
        }
        const formData = new FormData();
        if(this.state.fileList.length>0){
            formData.append("file",this.state.fileList[0]);
        }
        formData.append("summaryTemplet",JSON.stringify(params).toString());
        postAction("/ybt/summary/update",formData,0,2).then(res=>{
            if(res.success){
                message.success("添加成功");
                this.props.history.push("/systemSettings/summaryTemplateManagement")
            }else{
                message.error(res.obj)
            }
        })
    }
    //页面取消
    _cancle = () => {
        this.props.history.push("/systemSettings/summaryTemplateManagement")
    }
    
    //新增/编辑 字段弹窗的确认按钮---更新自定义表单内容
    _updateTableHead = (params) => {
        this.setState({showAddFieldModal:false,selectHeadInfo:{}})
        if(this.state.type==="add"){
            this.setState((state)=>({tableHead:[...state.tableHead,params]}))
        }else{
            let tableHead = this.state.tableHead.map((item,index)=>{
                if(index===this.state.editIndex){
                    return params
                }else{
                    return item
                }
            });
            this.setState({tableHead})
        }
    }
    //新增/编辑 字段弹窗的取消按钮
    _onCancel = () => {
        this.setState({showAddFieldModal:false})
    }
    //删除选中的字段
    removeTableHead = () => {
        this.state.tableHead.splice(this.state.editIndex,1)
        this.setState((state)=>({
            tableHead:state.tableHead
        }))
    }
    render() {
        const uploadProps = {
            onRemove: file => {
                this.setState({fileList:[]})
            },
            beforeUpload: file => {
                this.setState({fileList:[file]})
                return false;
            },
            fileList:this.state.fileList
        };
        return (
            <React.Fragment>
                <SystemTabs activeTab="summaryTemplateManagement"/>
                <div className={styles.container}>
                    <div className={`${styles.card} ${styles.addCard}`}>
                        <div className={styles.left}>
                            <div className={styles.top}>
                                选择数据集：
                                <Select onChange={this._changeSelect} value={this.state.dataSet} style={{width:200}}>
                                    {
                                        this.state.dataSetList.map(item=>(<Select.Option key={item.value} value={item.value}>{item.label}</Select.Option>))
                                    }
                                </Select>
                            </div>
                            <div className={styles.tree}>
                                <div className={styles.buttons}>
                                    <div style={{marginBottom:10,fontWeight:"bold",fontSize:"14px"}}>表单自定义</div>
                                    <Button onClick={()=>{this._showFieldModal("add")}} icon="plus" type="primary" style={{marginRight:10}}>新增字段</Button>
                                    <Button onClick={()=>{this._showFieldModal("edit")}} style={{marginRight:10}}>编 辑</Button>
                                    <Button onClick={this.removeTableHead}>删 除</Button>
                                </div>
                                <div className={styles.headerList}>
                                    {
                                        this.state.tableHead.map((item,index)=>{
                                            return <div onClick={()=>{this._selectHead(item,index)}} className={this.state.selectHeadInfo.dataIndex===item.dataIndex?styles.selected:""} key={item.dataIndex}>{item.title}</div>
                                        })
                                    }
                                </div>
                            </div>
                        </div>
                        <div className={styles.right}>
                            <div className={styles.top} style={{display:"flex"}}>
                                <div style={{padding:"0 10px",lineHeight:"32px"}}>汇总表名称：</div>
                                <div style={{flex:1}}><Input value={this.state.name} onChange={this._updateName}/></div>
                            </div>
                            <div className={styles.center}>
                                预览：
                                <div className={styles.table}>
                                    {this.state.tableHead.map(item=>{
                                        return <div key={item.dataIndex}>{item.title}</div>
                                    })}
                                </div>
                            </div>
                            <div className={styles.bottom}>
                                <div>
                                    上传最终导出模板：
                                    <Upload {...uploadProps}>
                                        <Button icon="upload">上传文件</Button>
                                    </Upload>
                                    {this.state.fileList.length===0?`原模板名称：${this.state.fileName}`:""}
                                </div>
                                <div style={{textAlign:"right"}}>
                                    <Button type="primary" onClick={this._submit}>保 存</Button>
                                    <Button style={{margin:"0 10px"}} onClick={this._cancle}>取 消</Button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <Modal
                    width={800}
                    visible={this.state.showAddFieldModal}
                    title={this.state.type==="add"?"新增汇总表字段（列）":"编辑汇总表字段（列）"}
                    onCancel={this._onCancel}
                    footer={false}
                    destroyOnClose={true}
                    bodyStyle={{padding:"0px"}}
                >
                    <FieldModal 
                        tableHead={this.state.tableHead}
                        dataSet={this.state.dataSet} 
                        fieldInfo={this.state.selectHeadInfo} 
                        onOk={(params)=>{this._updateTableHead(params)}}
                        onClose={this._onCancel}
                    />
                </Modal>
            </React.Fragment>
        )
    }
}


export default withRouter(Add);